import React, { Component } from 'react';
import styled from 'styled-components';
import Input from '../globalComponents/Input';
import StyledButton from '../globalComponents/StyledButton';

export class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            password: "",
        };
    }
    render() {
        return (
            <LoginWrapper>
                <Inside>
                    <img src="https://wildwest-rp.com/wp-content/uploads/2019/11/WILDWEST.png" alt="WildWest Logo"/>
                    <form>
                        <Input onChange={e => this.setState({ username : e.target.value })} placeholder="mikex" type="text" label="Username" />   
                        <Input onChange={e => this.setState({ password : e.target.value })} placeholder="**********" type="password" label="Password" />
                        <StyledButton text="Login" />
                    </form>
                </Inside>
            </LoginWrapper>
        );
    }
}

export default Login;

const LoginWrapper = styled.div`
    position: absolute;
    width: 600px;
    height: 400px;
    top: calc(50% - 200px);
    left: calc(50% - 300px);
    background: #232323eb;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    box-shadow: -2px 3px 6px 2px rgba(0, 0, 0, 0.49);
`;

const Inside = styled.div`  
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    height: 100%;

    img {
        width: 350px;
        margin: 10px;
    }

    h1 {
        font-size: 44px;
        text-transform: uppercase;
    }

    form {
        display: flex;
        flex-direction: column;
    }
`;